import React from 'react';
import { useHistory } from 'react-router-dom';

const Post = props => {
    const { index, id, title, time, desc, imgSrc } = props;
    const history = useHistory();
    const handleClick = () => history.push(`/blog/detail/${id}`);
    return (
        <div className={index === 0 ? `post-item post-item-${index} pointer` : 'post-item pointer'} style={{backgroundImage:`url('${imgSrc}')`}} onClick={handleClick}>
            <div className='post-detail'>
                <h3>{title}</h3>
                <label>{time}</label>
                <p>{desc}</p>
            </div>
        </div>
    )
}

export default Post;