import React from 'react';
import { useMediaQuery } from 'react-responsive';

const ListItem = ({ index, imgSrc, title, time, desc }) => {
    const isDesktop = useMediaQuery({ query: '(min-width: 1005px)' });
    return (
        <div className={`blog-list-item blog-list-item-${index} pointer${isDesktop ? '' : ' space'}`}>
            <img alt='item-preview' src={imgSrc} />
            <div className='blog-list-item-detail'>
                <h3>{title}</h3>
                <label>{time}</label>
                <p>{desc}</p>
            </div>
        </div>
    )
}

export default ListItem;